

.btn3 {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn3--primary {
  background-color: var(--primary2);
  color: #242424;
  border: 1px solid var(--primary2);
}

.btn3--outline {
  background-color: transparent;
  color:#c31432;
  padding: 8px 20px;
  border: 1px solid #c31432;
  transition: all 0.3s ease-out;
}

.btn3--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn3--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn3--large:hover,
.btn3--medium:hover {
  transition: all 0.3s ease-out;
  background:#c31432;
  color: white;
  transition: 250ms;
}
